body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app__header {
  display: flex;
  background-color: #FFFFFF;
  color: #F27663;
  position: fixed;
  box-shadow: 0 0 1em grey;
  width: 100%;
  z-index: 1; }

.button__container {
  transition: all 1s;
  padding: 0 30px;
  display: flex;
  justify-content: center;
  align-items: center; }

.div__btn {
  border: 0px;
  cursor: pointer; }

.zmdi-menu {
  padding: 10px;
  font-size: 30px;
  color: #52086F; }
  .zmdi-menu:hover {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f5d9f5;
    transition: 1s all; }

.span-style {
  color: #876E7B; }

.span-sage {
  color: #5A4463; }

.app__header-title {
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 300; }

.page__wrapper {
  height: 100vh;
  display: flex;
  position: relative;
  top: 83px; }

.app__container {
  width: 100%;
  display: flex;
  flex-direction: column; }

.hidden {
  display: none; }

.app__aside {
  width: 270px;
  height: 100vh;
  background: #FBF6FD;
  bottom: 0;
  transition: bottom ease 2s;
  position: relative; }

.app__aside-header {
  display: flex;
  align-items: center; }

.zmdi-chart {
  padding: 10px;
  font-size: 30px;
  color: #52086F; }

.app__aside-title {
  color: #52086F;
  margin-left: 10px;
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase; }

.aside__list {
  padding: 0;
  background-color: #FBF6FD;
  color: #F4ECF7;
  list-style: none;
  text-decoration: none;
  line-height: 24px; }

.aside__item-link {
  text-decoration: none;
  color: #854F9B;
  font-weight: bold; }

.aside__item {
  transition: background-color ease 1s; }
  .aside__item:hover {
    background-color: #f5d9f5; }

.panel__session {
  display: flex;
  align-items: center;
  margin: 20px; }

.icon {
  font-size: 18px; }

.panel__session-title {
  padding-left: 10px;
  font-size: 18px; }

.search__container {
  display: flex;
  align-items: center;
  margin-left: 10px;
  vertical-align: middle;
  white-space: nowrap;
  position: relative; }

.icon__search {
  position: absolute;
  top: 5%;
  margin-left: 17px;
  margin-top: 17px;
  z-index: 1;
  color: #4f5b66; }

.search__input {
  width: 300px;
  height: 50px;
  border: none;
  font-size: 10pt;
  float: left;
  color: #63717f;
  padding-left: 45px;
  outline: none;
  margin-bottom: 20px;
  border-bottom: 1px solid #E9E9E9;
  box-sizing: border-box; }
  .search__input:focus {
    border-bottom: 2px solid #854F9B;
    border-width: 2px; }

.search__info-total {
  padding-left: 10px; }

.table__container {
  display: flex; }

.table {
  width: 100%;
  table-layout: fixed;
  background-color: #FFFFFF;
  margin: 0;
  padding: 0; }

#table tr:nth-child(odd) {
  color: #7F7679; }

#table tr:nth-child(even) {
  background-color: #FFFFFF;
  color: #876E7B; }

.table__tr {
  background-color: #F4ECF7;
  width: 325px; }

.table__thead {
  margin: 0;
  padding: 0;
  color: #854F9B; }

.table__content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center; }

.table__title {
  margin-left: 10px;
  color: #854F9B;
  font-weight: bold; }

.table__icons {
  color: #854F9B;
  width: 20px;
  display: flex;
  flex-direction: column; }
  .table__icons i:hover {
    background-color: #FBF6FD;
    font-weight: bold;
    height: 20px;
    width: 20px; }

.table__td {
  margin: 0;
  padding: 0;
  padding: 10px; }

.breadcrumb__container {
  text-align: center; }

.breadcrumb__container-list {
  padding: 0 10px;
  display: flex;
  flex-direction: row;
  list-style: none; }

.breadcrumb__container-item {
  padding: 0 10px; }

.breadcrumb__link {
  text-decoration: none;
  transition: background-color ease 0.8s; }
  .breadcrumb__link:hover {
    background-color: #f5d9f5; }

.charts__container {
  display: flex; }

.chart__filter-title {
  margin-left: 10px;
  color: #52086F; }

.chart__filter-content {
  margin: 0px;
  padding: 10px 20px 20px; }

.chart__filter {
  font-family: "Roboto", sans-serif;
  background-color: #FFFFFF;
  border: 1px solid #FBF6FD;
  margin: 20px;
  width: 400px; }

.chart__filter-header {
  background-color: #FBF6FD;
  padding-left: 10px;
  display: flex;
  align-items: center; }

.chart__filter-options {
  height: 100px; }

.chart__filter-range {
  line-height: 32px; }

.chart__filter-groups {
  line-height: 32px; }

.chart__filter-listgroups {
  padding: 0;
  list-style: none; }

.input__type-radio {
  cursor: pointer; }

